import { Mix } from 'src/models/mix';
import { useSelector, useDispatch } from 'react-redux';
import { MainPlayerState, loadStart } from 'src/@slices/mainPlayer';
import { RootState } from 'src/@store';
import player from 'src/services/player';
import { download } from 'src/utils/drive';

const usePlayer = () => {
  const dispatch = useDispatch();
  const { currentMix, isPlaying, isPaused, isLoading, hasError, isVisible } = useSelector<
    RootState,
    MainPlayerState
  >((state) => state.mainPlayer);

  const seek = (seconds: number) => player.audio.currentTime = seconds;
  const pause = () => console.log('');
  const play = (mix?: Mix | null) => {
    if (
      (mix && currentMix && hasError) ||
      (!currentMix && mix) ||
      (currentMix && mix && mix.id !== currentMix.id)
    ) {
      dispatch(loadStart(mix));
      player.load(download(mix.sourceId));
      seek(0);
    } else {
      isPlaying && player.pause();
      isPaused && player.play();
    }
  };

  return {
    currentMix,
    play,
    pause,
    seek,
    hasError,
    isPlaying,
    isPaused,
    isLoading,
    isVisible,
    service: player,
  };
};

export default usePlayer;
