import React, { useState, useRef, useEffect } from 'react';
import usePlayer from 'src/hooks/usePlayer';
import TimelineCounter from 'src/components/timelineCounter';
import { calcUnit, ICalcUnit } from 'src/utils/functors';
import { Responsive } from 'semantic-ui-react';

const MiniTimeline = () => {
  const {
    service: { audio },
    currentMix,
    isPlaying,
    hasError,
  } = usePlayer();

  const { currentTime } = audio;
  const [startTime, setStartTime] = useState(currentTime);
  const [x, setX] = useState(0);
  const containerRef = useRef<HTMLDivElement>(null);
  const durationRef = useRef<number>();

  const onUpdate = () => {
    const el = containerRef.current || document.getElementById('mini-tl-wrapper');
    el && setStartTime(audio.currentTime);
  };

  const onCount = (seconds: number) => {
    const containerWidth = containerRef?.current?.clientWidth || 0;
    setX(
      calcUnit({ left: seconds, right: durationRef.current })
        .map(({ toPercent }: ICalcUnit) => {
          return calcUnit({ left: containerWidth, right: toPercent() });
        })
        .toNumber()
    );
  };

  // when current mix changed
  useEffect(() => {
    setX(0);
    setStartTime(0);
    durationRef.current = currentMix?.duration;
  }, [currentMix]);

  // when any exception throwed
  useEffect(() => {
    if (hasError) {
      setX(0);
      setStartTime(0);
    }
  }, [hasError]);

  return (
    <div ref={containerRef} id="mini-tl-wrapper">
      <Responsive
        fireOnMount
        className="mini-content"
        as="div"
        width={12}
        onUpdate={onUpdate}
      >
        <div style={{ width: `${x}px` }} />
        <TimelineCounter
          visible={false}
          current={startTime}
          duration={durationRef.current}
          active={isPlaying}
          onCount={onCount}
        />
      </Responsive>
    </div>
  );
};

export default MiniTimeline;
